import React, {useState, useRef, useEffect} from "react";
import {useWindowDimensions, Platform} from "react-native";
import { TouchableOpacity, Text } from "react-native";


import {
    View,
    StyleSheet,
    Animated,
    Dimensions,
    PanResponder,
    TouchableWithoutFeedback,
    Easing,
    ImageBackground,
    Keyboard,
} from "react-native";
import Modal from 'react-native-modalbox'
import {getTypeDevice} from "../GetTypeDeviceSize/GetTypeDeviceSize";

// const height = Dimensions.get("window").height-(Dimensions.get("window").height*0.3);

const ModalPage = (props) => {
    // 1)  (   )   
    const screenH = Dimensions.get('window').height; //  window!
    const initialScreenHRef = useRef(screenH);       //    

    // 2)        ( )
    const {height: winH, width: winW} = useWindowDimensions();

    // 3)   ,    
    const [keyboardVisible, setKeyboardVisible] = useState(false);

    useEffect(() => {
        const showSub = Keyboard.addListener(
            Platform.OS === 'ios' ? 'keyboardWillShow' : 'keyboardDidShow',
            () => setKeyboardVisible(true)
        );
        const hideSub = Keyboard.addListener(
            Platform.OS === 'ios' ? 'keyboardWillHide' : 'keyboardDidHide',
            () => setKeyboardVisible(false)
        );
        return () => {
            showSub.remove();
            hideSub.remove();
        };
    }, []);

    // 4)     
    const webHeight = Platform.OS === 'web' && typeof window !== 'undefined'
        ? window.innerHeight
        : 0;
    const STABLE_HEIGHT = Platform.OS === 'web'
        ? (winH || webHeight || initialScreenHRef.current)
        : initialScreenHRef.current;
    const STABLE_PADDING = STABLE_HEIGHT * 0.2;


    const TIMING_CONFIG = {
        duration: props.duration ? props.duration : 250,
        easing: Easing.inOut(Easing.ease),
    };

    const pan = useRef(new Animated.ValueXY()).current;

    const runOpenAnimation = (targetY) => {
        const toY = typeof targetY === 'number' ? targetY : STABLE_PADDING;
        setIsAnimating(true);
        Animated.timing(pan, {
            ...TIMING_CONFIG,
            toValue: {x: 0, y: toY},
            useNativeDriver: false,
        }).start(() => {
            setIsAnimating(false);
        });
    };

    let [isAnimating, setIsAnimating] = useState(
        props.DisableHandAnimation ? true : false
    );

    let animatedValueX = 0;

    let animatedValueY = STABLE_PADDING;

    const panResponder = useRef(
        PanResponder.create({
            // Ask to be the responder:
            onStartShouldSetPanResponder: () => false,
            onStartShouldSetPanResponderCapture: () => false,
            onMoveShouldSetPanResponder: (evt, gestureState) => {
                if (isAnimating) {
                    return false;
                }
                if (gestureState.dy > 22) {
                    return true;
                }
                return false;
            },
            onPanResponderGrant: () => {
                pan.setOffset({
                    x: animatedValueX,
                    y: animatedValueY,
                });
                pan.setValue({x: 0, y: 20}); // Initial value
            },
            onPanResponderMove: (evt, gestureState) => {
                if (gestureState.dy > 0) {
                    pan.setValue({x: 0, y: gestureState.dy});
                }
            },
            onPanResponderRelease: (evt, gestureState) => {
                // The user has released all touches while this view is the
                // responder. This typically means a gesture has succeeded
                // Flatten the offset so it resets the default positioning
                if (gestureState.dy > 0 && gestureState.vy > 0) {
                    if (gestureState.vy <= -0.7 || gestureState.dy <= -100) {
                        setIsAnimating(true);
                        Animated.timing(pan, {
                            toValue: {x: 0, y: -(STABLE_HEIGHT)},
                            ...TIMING_CONFIG,
                            useNativeDriver: false,
                        }).start(() => {
                            setIsAnimating(false);
                            props.onClose();
                        });
                    } else if (gestureState.vy >= 0.5 || gestureState.dy >= 100) {
                        setIsAnimating(true);
                        Animated.timing(pan, {
                            toValue: {x: 0, y: (STABLE_HEIGHT)},
                            ...TIMING_CONFIG,
                            useNativeDriver: false,
                        }).start(() => {
                            setIsAnimating(false);
                            props.onClose();
                        });
                    } else {
                        setIsAnimating(true);
                        Animated.spring(pan, {
                            toValue: 0,
                            useNativeDriver: false,
                        }).start(() => {
                            setIsAnimating(false);
                            // props.onClose();
                        });
                    }
                } else {
                    setIsAnimating(true);
                    Animated.spring(pan, {
                        toValue: 0,
                        useNativeDriver: false,
                    }).start(() => {
                        setIsAnimating(false);
                        // props.onClose();
                    });
                }
            },
        })
    ).current;


    const FOOTER_H = props.footerHeight || 64; //    /
    const footerBottom = useRef(new Animated.Value(0)).current; //   

    const [kbHeight, setKbHeight] = useState(0);
    useEffect(() => {
        const onShow = (e) => {
            const h = e?.endCoordinates?.height || 0;
            setKbHeight(h);
            Animated.spring(footerBottom, { toValue: h, useNativeDriver: false, bounciness: 0, speed: 20 }).start();
        };
        const onHide = () => {
            setKbHeight(0);
            Animated.spring(footerBottom, { toValue: 0, useNativeDriver: false, bounciness: 0, speed: 20 }).start();
        };

        const showSub = Keyboard.addListener(Platform.OS === 'ios' ? 'keyboardWillShow' : 'keyboardDidShow', onShow);
        const hideSub = Keyboard.addListener(Platform.OS === 'ios' ? 'keyboardWillHide' : 'keyboardDidHide', onHide);

        return () => { showSub.remove(); hideSub.remove(); };
    }, []);


    useEffect(() => {
        if (props.modalVisible) {
            if (Platform.OS === 'web' && typeof window !== 'undefined') {
                requestAnimationFrame(() => {
                    if (window.visualViewport && typeof window.visualViewport.dispatchEvent === 'function') {
                        window.visualViewport.dispatchEvent(new Event('resize'));
                    } else {
                        window.dispatchEvent(new Event('resize'));
                    }
                });
            }
            animatedValueX = 0;
            animatedValueY = 0;
            pan.setOffset({
                x: animatedValueX,
                y: animatedValueY,
            });
            if (Platform.OS === 'web') {
                pan.setValue({ x: 0, y: STABLE_HEIGHT });
            } else {
                pan.setValue({
                    x: 0,
                    y: props.OpenModalDirection == "up" ? -(STABLE_HEIGHT + STABLE_PADDING) : (STABLE_HEIGHT + STABLE_PADDING),
                }); // Initial value
            }
            pan.x.addListener((value) => (animatedValueX = value.value));
            pan.y.addListener((value) => (animatedValueY = value.value));
            if (Platform.OS === 'web') {
                runOpenAnimation(0);
            }
        }
        console.log(' 1 useEffect');
    }, [props.modalVisible]);

    useEffect(() => {
        if (props.PressToanimate) {
            setIsAnimating(true);
            Animated.timing(pan, {
                toValue: {
                    x: 0,
                    y: props.PressToanimateDirection == "up" ? -(STABLE_HEIGHT + STABLE_PADDING) : (STABLE_HEIGHT + STABLE_PADDING),
                },
                ...TIMING_CONFIG,
                useNativeDriver: false,
            }).start(() => {
                setIsAnimating(false);
                props.onClose();
            });
        }
        console.log(' 2 useEffect');
    }, [props.PressToanimate]);

    let handleGetStyle = (opacity) => {
        const panTransform = Platform.OS === 'web'
            ? { transform: [{ translateX: pan.x }, { translateY: pan.y }] }
            : null;
        return [
            [
                styles.container,
                {
                    // transform: [{ translateX: pan.x }, { translateY: pan.y }],
                    opacity: opacity,
                },
                panTransform ? panTransform : null,
                [props.HeaderStyle],
            ],
        ];
    };

    let handleGetStyleBody = (opacity) => {
        const panTransform = Platform.OS === 'web'
            ? { transform: [{ translateX: pan.x }, { translateY: pan.y }] }
            : null;
        return [
            [
                styles.background,
                {
                    // transform: [{ translateX: pan.x }, { translateY: pan.y }],
                    opacity: opacity,
                },
                panTransform ? panTransform : null,
            ],
            [props.ContentModalStyle],
        ];
    };
    let handleMainBodyStyle = (opacity) => {
        return [
            [
                styles.ContainerModal,
                {
                    opacity: opacity,
                },
            ],
            [props.MainContainerModal],
        ];
    };

    let interpolateBackgroundOpacity = pan.y.interpolate({
        inputRange: [-(STABLE_HEIGHT), 0, (STABLE_HEIGHT)],
        outputRange: [props.fade ? 0 : 1, 1, props.fade ? 0 : 1],
    });


    const modalBody = (
        <Animated.View
            style={[StyleSheet.absoluteFill, handleMainBodyStyle(interpolateBackgroundOpacity)]}
        >
            <Animated.View
                style={handleGetStyleBody(interpolateBackgroundOpacity)}
                {...panResponder.panHandlers}
            >
                <TouchableWithoutFeedback
                    onPress={() => Keyboard.dismiss()}
                    style={styles.TouchWithoutFeedBack}
                >
                    <ImageBackground
                        source={props.ImageBackgroundModal && props.ImageBackgroundModal}
                        style={
                            getTypeDevice(Dimensions.get('window').width, Dimensions.get('window').height) === false
                                ? [styles.ImageBackground, {height: STABLE_HEIGHT - 17}] // <-- 
                                : styles.ImageBackground
                        }
                        imageStyle={props.ImageBackgroundModalStyle && props.ImageBackgroundModalStyle}
                    >
                        {/*<View style={{ flex: 1, paddingBottom: FOOTER_H + 12 }}>*/}
                            {props.ContentModal}
                        {/*</View>*/}
                    </ImageBackground>
                </TouchableWithoutFeedback>
            </Animated.View>
            <Animated.View
                style={handleGetStyle(interpolateBackgroundOpacity)}
                {...panResponder.panHandlers}
            >
                <TouchableWithoutFeedback>
                    {props.HeaderContent ? props.HeaderContent : <View/>}
                </TouchableWithoutFeedback>
            </Animated.View>
            {/*<Animated.View*/}
            {/*    pointerEvents="auto"*/}
            {/*    style={[*/}
            {/*        styles.BottomBar,*/}
            {/*        {*/}
            {/*            height: FOOTER_H,*/}
            {/*            bottom: footerBottom, //   ,   */}
            {/*        },*/}
            {/*    ]}*/}
            {/*>*/}
            {/*    /!*    /.         *!/*/}
            {/*    {props.FooterFixed ? props.FooterFixed : <View />}*/}
            {/*</Animated.View>*/}
            {getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? false : Dimensions.get('window').width >460 ? false  :
             <Animated.View
                style={styles.CloseButtonWrapper}
                pointerEvents="auto"
            >
                <TouchableOpacity
                    style={styles.CloseButton}
                    onPress={props.onClose}
                    activeOpacity={0.7}
                >
                    <Text style={styles.CloseButtonText}></Text>
                </TouchableOpacity>
            </Animated.View>
                }
        </Animated.View>
    );

    if (Platform.OS === 'web') {
        if (!props.modalVisible) {
            return null;
        }
        let portal = null;
        if (typeof document !== 'undefined') {
            try {
                portal = require('react-dom').createPortal;
            } catch (err) {
                portal = null;
            }
        }
        const webContent = (
            <View style={styles.webPortal}>
                {modalBody}
            </View>
        );
        return portal && typeof document !== 'undefined'
            ? portal(webContent, document.body)
            : webContent;
    }

    return (
        <Modal
            animationType="none"
            transparent={true}
            isOpen={props.modalVisible}
            position={'bottom'}

            coverScreen={Platform.OS !== 'web'}                // <<   Android
            backdrop={true}
            swipeToClose={false}
            useNativeDriver={Platform.OS !== 'web'}
            //       :
            style={[{height: STABLE_HEIGHT, justifyContent: 'flex-end', backgroundColor: 'transparent'}]}


            onShow={() => {
                runOpenAnimation();
            }}
            onClosed={props.onClose}
            // style={{height:"50%"}}
        >
            {modalBody}
        </Modal>
    );
};

const styles = StyleSheet.create({
    CloseButtonWrapper: {
        position: 'absolute',
        top: Platform.OS === 'ios' ? 10 : 5, //    
        right: 10,
        zIndex: 20,
    },
    CloseButton: {
        width: 20,
        height: 20,
        borderRadius: 18,
        backgroundColor: 'rgba(0,0,0,0.5)',
        justifyContent: 'center',
        alignItems: 'center',
    },
    CloseButtonText: {
        color: '#fff',
        fontSize: 14,
        fontWeight: 'bold',
        lineHeight: 10,
    },
    BottomBar: {
        position: 'absolute',
        left: 0,
        right: 0,
        //       
        backgroundColor: '#fff',
        justifyContent: 'center',
        paddingHorizontal: 16,
        //  iOS
        shadowColor: '#000',
        shadowOpacity: 0.08,
        shadowOffset: { width: 0, height: -2 },
        shadowRadius: 10,
        //  Android
        elevation: 8,
        //   :
        borderTopLeftRadius: 12,
        borderTopRightRadius: 12,
    },
    background: {
        opacity: 0,
        flex: 1,
        marginTop: 55,
        borderRadius: 10
    },
    container: {
        marginTop: 50,
        position: "absolute",
        width: "100%",
    },
    ContainerModal: {
        backgroundColor: "rgba(0, 0, 0, 0.5)",
        flex: 1,
    },
    ImageBackground: {
        width: "100%",
        height: "100%",
        // height:Dimensions.get('window').height-17
    },
    TouchWithoutFeedBack: {
        flex: 1,

        // height:Dimensions.get('window').height+10
    },
    webPortal: {
        position: 'fixed',
        top: 0,
        right: 0,
        bottom: 0,
        left: 0,
        zIndex: 9999,
        display: 'flex',
        pointerEvents: 'auto',
    },
});

export default ModalPage;
