const CITY_STORAGE_KEY = "cityData";
const BASKET_STORAGE_KEY = "basketByCity";
const LEGACY_BASKET_KEY = "basket";
const PROMOKODE_STORAGE_KEY = "promokode";

const normalizeBool = (value) => {
  if (value === true || value === 1) {
    return true;
  }
  if (typeof value === "string") {
    return value === "1" || value.toLowerCase() === "true";
  }
  return false;
};

const normalizeCityData = (data) => {
  if (!data || typeof data !== "object") {
    return null;
  }
  if (data.cityId || data.cityName) {
    return {
      cityId: data.cityId,
      cityName: data.cityName
    };
  }
  if (data.idCity || data.name) {
    return {
      cityId: data.idCity,
      cityName: data.name
    };
  }
  return null;
};

const readStoredCity = () => {
  if (typeof localStorage === "undefined") {
    return null;
  }
  try {
    const parsed = JSON.parse(localStorage.getItem(CITY_STORAGE_KEY));
    return normalizeCityData(parsed);
  } catch (e) {
    return null;
  }
};

const resolveCityFromList = (candidate, cityListMap) => {
  if (!candidate?.cityId || !cityListMap) {
    return null;
  }
  const key = String(candidate.cityId);
  if (!cityListMap[key]) {
    return null;
  }
  return {
    cityId: key,
    cityName: cityListMap[key]
  };
};

const readBasketStorage = () => {
  if (typeof localStorage === "undefined") {
    return {};
  }
  try {
    const parsed = JSON.parse(localStorage.getItem(BASKET_STORAGE_KEY));
    if (parsed && typeof parsed === "object" && !Array.isArray(parsed)) {
      return parsed;
    }
  } catch (e) {
    return {};
  }
  return {};
};

const writeBasketStorage = (data) => {
  if (typeof localStorage === "undefined") {
    return;
  }
  localStorage.setItem(BASKET_STORAGE_KEY, JSON.stringify(data || {}));
  localStorage.removeItem(LEGACY_BASKET_KEY);
};

const getStoredBasketForCity = (cityId) => {
  if (typeof localStorage === "undefined") {
    return [];
  }
  const key = String(cityId || "");
  if (!key) {
    return [];
  }
  const map = readBasketStorage();
  if (Array.isArray(map[key])) {
    return map[key];
  }
  try {
    const legacy = JSON.parse(localStorage.getItem(LEGACY_BASKET_KEY));
    if (Array.isArray(legacy)) {
      map[key] = legacy;
      writeBasketStorage(map);
      return legacy;
    }
  } catch (e) {
    return [];
  }
  return [];
};

const setStoredBasketForCity = (cityId, basket) => {
  if (typeof localStorage === "undefined") {
    return;
  }
  const key = String(cityId || "");
  if (!key) {
    return;
  }
  const map = readBasketStorage();
  map[key] = Array.isArray(basket) ? basket : [];
  writeBasketStorage(map);
};

const clearStoredBasketForCity = (cityId) => {
  if (typeof localStorage === "undefined") {
    return;
  }
  const key = String(cityId || "");
  const map = readBasketStorage();
  if (key && Object.prototype.hasOwnProperty.call(map, key)) {
    delete map[key];
    writeBasketStorage(map);
    return;
  }
  localStorage.removeItem(BASKET_STORAGE_KEY);
  localStorage.removeItem(LEGACY_BASKET_KEY);
};


