export const loadCity = (cityData) => {
  return async dispatch => {
    //const DelAllcityData = await DB.delAllCity();
    const activeThemeData = pathConfig[ActiveSiteSettings.THEMENAME] || null;
    const defaultCityData = activeThemeData ? activeThemeData.cityData : { cityId: 1, cityName: "" };
    // const cityData = await DB.getActiveCity();
    //const cityData = {idCity:1,name:""};

    const cityListSystem = await ServerDB.getCity();

    //    sqlite      

    let CityListAr = {};
    Object.keys(cityListSystem.list || {}).forEach((d,n)=>{
      CityListAr[cityListSystem.list[d].id]=cityListSystem.list[d].title
    });

    const storedCity = readStoredCity();
    const fromListStored = resolveCityFromList(storedCity, CityListAr);
    const fromListDefault = resolveCityFromList(normalizeCityData(defaultCityData), CityListAr);
    const firstCityId = Object.keys(CityListAr)[0];
    const firstCity = firstCityId ? { cityId: firstCityId, cityName: CityListAr[firstCityId] } : null;
    const resolvedCity = fromListStored || fromListDefault || firstCity || normalizeCityData(defaultCityData);

      dispatch({
        type: SET_CITY_LIST,
        payload: CityListAr
        // payload: {cityId:1,cityName:''}
      });

      dispatch({
        type: SET_CITY_ID,
        payload: {cityId:resolvedCity?.cityId,cityName:resolvedCity?.cityName}
        // payload: {cityId:1,cityName:''}
      });
    // }
    if (typeof localStorage !== "undefined" && resolvedCity?.cityId) {
      localStorage.setItem(CITY_STORAGE_KEY, JSON.stringify(resolvedCity));
    }
  };
};


export const loadCurrentVersionApp = (nowVersionApp) => {
  return async dispatch => {
    const Version = await ServerDB.getCurrentVersionApp();

    if(Version === false){

    }else{
      if(nowVersionApp*1 < Version.currentVersionGoogle*1) {
        dispatch({
          type: SET_UPDATE_APP_STATUS,
          payload: {
            updateAppStatus:true,
            linkUpdateAppiOS:Version.linkUpdateAppiOS,
            linkUpdateAppAndroid:Version.linkUpdateAppAndroid,
            linkUpdateAppAndroidNoGoogle:Version.linkUpdateAppAndroidNoGoogle,
            linkUpdateAppAndroidXiaomi:Version.linkUpdateXiaomi,
          }
        });
      }
    }
    setTimeout(()=>{dispatch(loadCurrentVersionApp(nowVersionApp));},20000);
  };

};


