export const loadAuthInfo = (timeOuts = false, reloads = false, PushNotification = null, devicePushToken = false, typeToken = null, UserVKDataState = {}, idUserSystem = null ) => {
  const currentState = store.getState();
  const postData = currentState.post;
  let CityId = postData.cityData.cityId;
  return async dispatch => {
    let CityData = 5;
    const AuthData = UserVKDataState;

    let Obj = Object.keys(AuthData);
    if(Obj.length >0) {

      // const AuthServer = await ServerDB.VerificationTokenServer(AuthData.rows[0].phone,AuthData.rows[0].accessToken,CityId);
      let AuthServer = await ServerDB.VerificationIdVkServer(AuthData.id,CityId);

      if(AuthServer?.phone){
        AuthServer = await ServerDB.VerificationTokenServerVkApps(AuthServer?.phone,null,CityId);
      }
      // if(PushNotification !== null & PushNotification !== false){
      //   await ServerDB.setUserTokenPushServer(AuthData.rows[0].idUser,PushNotification,devicePushToken,typeToken);
      // }

      if(AuthServer?.id || AuthServer?.result === "SUCCESS"){
        // await dispatch({
        //   type: SET_AUTH_USER_DATA,
        //   payload: {
        //     id:AuthData.rows._array[0].idUser,
        //     phone:AuthData.rows._array[0].phone,
        //     accessToken:AuthData.rows._array[0].accessToken,
        //     UserDataServer:AuthServer.dataUserServer,
        //     BasketServerSettings:AuthServer.dataBasketServer
        //   }
        // });

        await dispatch({
          type: SET_AUTH_USER_DATA,
          payload: {
            id:AuthServer.dataUserServer.id,
            phone:AuthServer.dataUserServer.phone,
            accessToken:null,
            UserDataServer:AuthServer.dataUserServer,
            BasketServerSettings:AuthServer.dataBasketServer,
            activeLinkDeletedProfile:AuthServer.activeLinkDeletedProfile || false,
            WorkTimePointsCity: AuthServer.WorkTimePointsCity || {
              id_city:CityId,
              resultWorkTime: true,
              pre_order: true,
              textInformer: "   ,    ,   !",
              textInformerPreorder: "      ,     !"
            }
          }
        });
        await dispatch({
          type: SET_POINTS_SYSTEM_SETTINGS,
          payload: {
            bonusPointsText:
              AuthServer.pointsSystemText
            ,
            bonusPointsActive:AuthServer.activeBonusPointsMobile,
            bonusPointsAffectDelivery: normalizeBool(
                AuthServer?.bonusPointsAffectDelivery ??
                AuthServer?.bonusPointsDeliveryMode ??
                AuthServer?.bonusPointsDeliveryRecalc ??
                AuthServer?.bonusPointsFreeDeliveryRecalc ??
                AuthServer?.pointsSystemSettings?.bonusPointsAffectDelivery
            ),
            textRecommendBlockCart:AuthServer.textRecommendBlockCart,
            textDopProductBlockCart:AuthServer.textDopProductBlockCart,
            textPresetsGoodsBlockCart:AuthServer.textPresetsGoodsBlockCart,
            dopInfoBlockBasket:AuthServer.dopInfoBlockBasket,
            positionOrdersBlock:AuthServer?.positionOrdersBlock || ["dopProduct","Recommend"],
            callOperatorInfo:AuthServer?.callOperatorInfo == 1,
            personCountActive:AuthServer?.personCountActive == 1,
        }});

        if(AuthServer.dataUserServer.birthday.length > 0){
          await dispatch({
            type:SET_BDAY_ALL_INFO,
            payload:{
              BDayAllInfo:{
                BDayActivity:AuthServer?.BDaySettings?.BDayActivity || true,//       
                BDayCountBefore:AuthServer?.BDaySettings?.BDayCountBefore || 3,// -     
                BDayCountAfter:AuthServer?.BDaySettings?.BDayCountAfter || 3,// -     
                BDayDiscountPrice:AuthServer?.BDaySettings?.BDayDiscountPrice || 10,//    
                BDay_action_adition:AuthServer?.BDaySettings?.BDay_action_adition || true,//    
                BDay_gift_adition:AuthServer?.BDaySettings?.BDay_gift_adition || true,//    
                BDay_threeForTwo_adition:AuthServer?.BDaySettings?.BDay_threeForTwo_adition || true,//  3=2  
              }
            }
          }) ///         

          const BDayAllInfo = {
            BDayActivity:AuthServer?.BDaySettings?.BDayActivity || false,//       
            BDayCountBefore:AuthServer?.BDaySettings?.BDayCountBefore || 0,// -     
            BDayCountAfter:AuthServer?.BDaySettings?.BDayCountAfter || 0,// -     
            BDayDiscountPrice:AuthServer?.BDaySettings?.BDayDiscountPrice || 10,//    
            BDay_action_adition:AuthServer?.BDaySettings?.BDay_action_adition || false,//    
            BDay_gift_adition:AuthServer?.BDaySettings?.BDay_gift_adition || false,//    
            BDay_threeForTwo_adition:AuthServer?.BDaySettings?.BDay_threeForTwo_adition || false,//  3=2  
          }

          if(BDayAllInfo.BDayActivity === true){
                let date = new Date()

                let tmpD=AuthServer.dataUserServer.birthday

                let PlusMinus=false

                let bDate =new Date()

                bDate.setDate(tmpD.split(".")[0])
                bDate.setMonth((tmpD.split(".")[1]*1-1))

            console.log("bDate",bDate)

                date=date.setHours(0,0,0,0)
            console.log("date",date)

            let MinusDate = 0;
            let PlusDate = 0;

            if((BDayAllInfo.BDayCountAfter === 0 || false) && (BDayAllInfo.BDayCountBefore === 0 || false)){
              //      ,    0 -       00   23:59
              MinusDate = date.valueOf();
              PlusDate = (date.valueOf()+8.64e+7)-1;
            }else{
              MinusDate = date.valueOf()-(BDayAllInfo.BDayCountBefore*(8.64e+7))
              if(BDayAllInfo.BDayCountAfter === 0 || false){
                //   0 -       23:59           
                PlusDate = (date.valueOf()+8.64e+7)-1;
              }else {
                PlusDate = date.valueOf()+(BDayAllInfo.BDayCountAfter*(8.64e+7))
              }

            }
                // let MinusDate = date.valueOf()-(2.592e+8)
                // let PlusDate = date.valueOf()+(2.592e+8) / c      3 (     )
                if(bDate.valueOf()>=MinusDate){
                  if(bDate.valueOf() <= PlusDate){
                    PlusMinus=true
                  }
                }

                // BDay=PlusMinus
                console.log("PlusMinus",PlusMinus)
                console.log("AuthServer",AuthServer)
                await dispatch({
                  type: SET_BDAY_STATUS,
                  payload:{
                    BDay:PlusMinus
                  }
                })
                await dispatch({
                  type: SET_BDAY_DISCOUNT,
                  payload:{
                    BDayDiscount:BDayAllInfo.BDayDiscountPrice
                  }
                })
              }
          }

        await dispatch(setStatusAUTHUserPhone(true));
        await dispatch(setStatusAUTHUserPhonePreloader(true));
      }else{
        await dispatch(setStatusAUTHUserPhone(false));
        await dispatch(setStatusAUTHUserPhonePreloader(true));
      }
    }else{
      await dispatch(setStatusAUTHUserPhonePreloader(true));
    }

    // if(reloads === true) {
    //   setTimeout(() => {
    //     dispatch(loadAuthInfo(false,false));
    //   }, 2000);
    // }


    return true;

  };

};

