import React, { useEffect, useRef, useState } from "react";
import {Alert, Animated, Modal, StyleSheet, Text, Pressable, View, Platform, TouchableOpacity, Easing} from "react-native";
import {THEME} from "../theme";
import { Ionicons } from '@expo/vector-icons';
import {useDispatch, useSelector} from "react-redux";
import {
    setCitySystem,
    setCityUserSystemPhone
} from "../store/actions/post";
import { MaterialIcons } from '@expo/vector-icons';



export const CityModal = ({titleCity,keys}) => {
    const [modalVisible, setModalVisible] = useState(false);
    const [webVisible, setWebVisible] = useState(false);

    const webOpacity = useRef(new Animated.Value(0)).current;
    const webTranslateY = useRef(new Animated.Value(12)).current;

    const dispatch = useDispatch();

    const CityList = useSelector(state => state.post.cityList);
    const CityDatas = useSelector(state => state.post.cityData);


    useEffect(() => {
        if (Platform.OS !== 'web') {
            return;
        }
        if (modalVisible) {
            setWebVisible(true);
            webOpacity.setValue(0);
            webTranslateY.setValue(12);
            Animated.parallel([
                Animated.timing(webOpacity, {
                    toValue: 1,
                    duration: 220,
                    easing: Easing.out(Easing.ease),
                    useNativeDriver: false,
                }),
                Animated.timing(webTranslateY, {
                    toValue: 0,
                    duration: 220,
                    easing: Easing.out(Easing.ease),
                    useNativeDriver: false,
                })
            ]).start();
        } else {
            Animated.parallel([
                Animated.timing(webOpacity, {
                    toValue: 0,
                    duration: 160,
                    easing: Easing.in(Easing.ease),
                    useNativeDriver: false,
                }),
                Animated.timing(webTranslateY, {
                    toValue: 12,
                    duration: 160,
                    easing: Easing.in(Easing.ease),
                    useNativeDriver: false,
                })
            ]).start(() => {
                setWebVisible(false);
            });
        }
    }, [modalVisible, webOpacity, webTranslateY]);

    const modalContent = (
        <Animated.View
            style={[
                styles.centeredViewWindow,
                {
                    opacity: Platform.OS === 'web' ? webOpacity : 1,
                    transform: Platform.OS === 'web' ? [{ translateY: webTranslateY }] : [],
                }
            ]}
        >
            <View style={styles.modalView}>
                <Text style={styles.modalText}> </Text>
                {Object.keys(CityList).map((data,id)=>{

                    return(
                    <Pressable
                        style={styles.buttonCity}
                        onPress={() => {
                            if(CityDatas.cityId !== data) {
                                dispatch(setCitySystem({cityId: data, cityName: CityList[data]}));
                                dispatch(setCityUserSystemPhone({cityId: data, cityName: CityList[data]}));
                            }
                            setModalVisible(!modalVisible);

                            //dispatch(loadSlideHeaderStatus());
                            // dispatch(loadSlideHeader(CityDatas));
                            // dispatch(loadCatalogProduct(CityDatas));
                        }}
                        key={"CityLink_"+id}
                    >
                        {/*<Text style={styles.textStyle}>{data} - {CityList[data]}</Text>*/}
                        <Text style={styles.textStyle}>{CityList[data]}</Text>
                    </Pressable>
                    );
                })}
            </View>
        </Animated.View>
    );

    return (
        <View style={modalVisible ? styles.centeredView : styles.centeredView} key={keys}>
            {Platform.OS === 'web' ? (
                webVisible ? (() => {
                    let portal = null;
                    if (typeof document !== 'undefined') {
                        try {
                            portal = require('react-dom').createPortal;
                        } catch (err) {
                            portal = null;
                        }
                    }
                    const webContent = (
                        <View style={styles.webPortal}>
                            {modalContent}
                        </View>
                    );
                    return portal && typeof document !== 'undefined'
                        ? portal(webContent, document.body)
                        : webContent;
                })() : null
            ) : (
                <Modal
                    animationType="fade"
                    transparent={true}
                    visible={modalVisible}
                    onRequestClose={() => {
                        // Alert.alert("   ");
                        setModalVisible(!modalVisible);
                    }}
                >
                    {modalContent}
                </Modal>
            )}
            <TouchableOpacity
                activeOpacity={0.7}
                style={{width:"100%"}}
            >
                <Pressable
                    activeOpacity={0.7}
                    onPress={() => setModalVisible(true)}

                    style={({ pressed }) => [
                        {
                            opacity: pressed
                                ? 0.8
                                : 1
                        }
                    ]
                    }

                >
                    <View style={Platform.OS === 'android' ? styles.LocationName : styles.LocationNameIos}>
                        <Text style={{fontSize:20}}>{titleCity}</Text>
                        <Text style={{justifyContent:"flex-end",alignItems:"flex-end",display:"contents"}}><MaterialIcons name="keyboard-arrow-down" size={28} color={Platform.OS === 'android' ? "#333333" : "#333333"} /></Text>
                    </View>
                </Pressable>
            </TouchableOpacity>
        </View>
    );
};

const styles = StyleSheet.create({
    centeredViewActive: {
        flex: 1,
        justifyContent: "center",
        alignItems: "center",
        marginTop: 0,
        // backgroundColor:"rgba(0,0,0,0.40)"
    },
    centeredView: {
        // flex: 1,
        justifyContent: "center",
        alignItems: "center",
        marginTop: 10,
        //backgroundColor:"rgba(0,0,0,0.40)"
    },
    centeredViewWindow: {
        flex: 1,
        justifyContent: "center",
        alignItems: "center",
        marginTop: 0,
        backgroundColor:"rgba(0,0,0,0.40)"
    },
    modalView: {
        margin: 20,
        backgroundColor: "white",
        borderRadius: 20,
        padding: 35,
        alignItems: "center",
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 2
        },
        shadowOpacity: 0.25,
        shadowRadius: 4,
        elevation: 5
    },
    button: {
        borderRadius: 20,
        padding: 10,
        elevation: 2
    },
    buttonOpen: {
        backgroundColor: "#F194FF",
    },
    buttonClose: {
        backgroundColor: "#2196F3",
    },
    textStyle: {
        color: "white",
        fontWeight: "bold",
        textAlign: "center"
    },
    modalText: {
        marginBottom: 15,
        textAlign: "center"
    },
    LocationName:{
        fontFamily:"open-bold, segoe ui, Arial",
        fontSize:20,
        color:"#333333",
        paddingHorizontal:11,
        textAlign:"left",
        flexDirection:'row',
        justifyContent:"flex-start",
        alignItems:"flex-start",
        width:"100%",
        // backgroundColor:"#ff7300"
    },
    LocationNameIos:{
        fontFamily:"open-bold, segoe ui, Arial",
        fontSize:20,
        color:"#333333",
        paddingHorizontal:11,
        flexDirection:'row',
        justifyContent:"flex-start",
        alignItems:"flex-start",
    },
    buttonCity:{
        paddingVertical: 10,
        paddingHorizontal: 25,
        elevation: 2,
        marginVertical:10,
        marginHorizontal:"5%",
        color:THEME.TEXT_COLOR,
        fontFamily: "open-bold, segoe ui, Arial",
        fontSize:20,
        backgroundColor:THEME.MAIN_COLOR,
        borderRadius:8
    },
    webPortal: {
        position: 'fixed',
        top: 0,
        right: 0,
        bottom: 0,
        left: 0,
        zIndex: 9999,
        display: 'flex'
    }
});
